<template>
  <div class="research-article--wrap">
    <div class="research-article-title">
      <img v-if="image"
           class="research-image"
           :src="`${imagePath}${image}`"
           alt="Research Image">
      <img v-else class="research-image"
           :src="`${imagePath}placeholder.svg`"
           alt="Research Image">
      <p class="title section-text-1" v-html="title"></p>
      <p class="field-and-year section-text-4 italic" v-html="fieldAndYear"></p>
    </div>
    <div class="research-article-content">
      <p class="content section-text-2" v-html="content"></p>
      <img v-if="image"
           class="research-image"
           :src="`${imagePath}${image}`"
           alt="Research Image">
      <img v-else class="research-image"
           :src="`${imagePath}placeholder.svg`"
           alt="Research Image">
    </div>
  </div>
</template>

<script>
export default {
  name: 'ResearchArticle',
  props: {
    title: {
      type: String,
      default: 'Research Title'
    },
    field: {
      type: Array,
      default () {
        return []
      }
    },
    year: {
      type: Number,
      default: 0
    },
    content: {
      type: String,
      default: 'Research Content'
    },
    image: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      imagePath: `${process.env.BASE_URL}contents/research/img/`
    }
  },
  computed: {
    fieldAndYear () {
      return `${this.field.join(', ')} | ${this.year}`
    }
  }
}
</script>

