    .research-image {
      display: none;
    }

    .title, .field-and-year {
      float: left;
      width: 100%;
    }
  }

  .research-article-content {
    float: left;
    width: 100%;
    vertical-align: top;

    .content {
      float: left;
      width: 70%;
      line-height: 24px;
    }

    .research-image {
      float: right;
      width: 25%;
    }
  }
}

