<template>
  <div class="navbar">
    <img class="logo" src="../assets/img/logo.svg" alt="DxD Lab" @click="navigate('Home')">
    <div class="nav--wrap">
      <ul class="nav-list">
        <li v-for="(navItem, index) in navList"
            class="nav-item"
            :class="{ selected: isSelected(navItem) }"
            :key="index"
            @click="navigate(navItem)">
          {{ navItem }}
        </li>
      </ul>
      <img class="hamburger-button" src="../assets/img/icon/hamburger.svg"
           alt="Hamburger" @click.stop="toggleHamburger">
    </div>
    <transition name="slide">
      <div v-if="showHamburger" class="hamburger--wrap">
        <ul class="hamburger-list">
          <li v-for="(navItem, index) in navList"
              class="hamburger-item"
              :class="{ selected: isSelected(navItem) }"
              :key="index"
              @click="navigate(navItem)">
            {{ navItem }}
          </li>
        </ul>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'AppHeader',
  computed: {
    currentPath () {
      const paths = []
      this.$route.matched.forEach(match => {
        paths.push(match.name)
      })
      return paths
    }
  },
  data () {
    return {
      navList: [
        'Home',
        'People',
        'Research',
        'Publications',
        'Courses',
        'News'
      ],
      showHamburger: false
    }
  },
  watch: {
    showHamburger (value) {
      let listener = () => {
        this.showHamburger = false
      }

      if (value) {
        document.body.addEventListener('click', listener, false)
      } else {
        document.body.removeEventListener('click', listener)
      }
    }
  },
  methods: {
    navigate (navItem) {
      for (let i = 0; i < this.currentPath.length; i++) {
        if (this.currentPath[i] === navItem) {
          return
        }
      }
      this.$router.push({ name: navItem })
    },
    isSelected (navItem) {
      for (let i = 0; i < this.currentPath.length; i++) {
        if (this.currentPath[i] && this.currentPath[i].indexOf(navItem) > -1) {
          return true
        }
      }
      return false
    },
    toggleHamburger () {
      this.showHamburger = !this.showHamburger
    }
  }
}
</script>

