<template>
  <div class="person--wrap">
    <img v-if="image"
         class="person-image"
         :src="`${imagePath}${image}`"
         alt="Person Image">
    <img v-else class="person-image"
         :src="`${imagePath}placeholder.svg`"
         alt="Person Image">
    <div class="person-info">
      <p class="name section-text-1" v-html="name"></p>
      <p class="position section-text-2" v-html="position"></p>
      <p class="email section-text-4 italic" v-html="email"></p>
      <p class="field section-text-3 bold" v-html="fieldToString"></p>
      <div v-if="profile.length" class="profile--wrap">
        <p v-for="(record, index) in profile"
           :key="index" v-html="record"
           class="profile section-text-4"></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Person',
  props: {
    name: {
      type: String,
      default: 'Name'
    },
    position: {
      type: String,
      default: 'Position'
    },
    email: {
      type: String,
      default: 'Email'
    },
    profile: {
      type: Array,
      default () {
        return []
      }
    },
    field: {
      type: Array,
      default () {
        return []
      }
    },
    image: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      imagePath: `${process.env.BASE_URL}contents/people/img/`
    }
  },
  computed: {
    fieldToString () {
      return this.field.join(', ')
    }
  }
}
</script>

