  }

  .person-info {
    display: inline-block;
    vertical-align: top;
    padding-left: 20px;
    width: 350px;

    .email {
      padding-top: 5px;
      padding-bottom: 30px;
    }

    .field {
      line-height: 20px;
      padding-bottom: 28px;
    }

    .profile--wrap {
      .profile {
