<template>
  <div class="news">
    <div class="news-header">
      <img class="news-image" src="../assets/img/navbar-image/news.svg" alt="News">
    </div>
    <div class="news-contents">
      <news-article v-for="(news, index) in newsList" :key="index"
                    :news-title="news.title" :news-date-raw="news.date"
                    :news-content="news.content"/>
    </div>
  </div>
</template>

<script>
import contents from '../../public/contents/news/text'
import NewsArticle from '@/components/news/NewsArticle'
import { compareDate } from '@/libs/utils'

export default {
  name: 'News',
  components: {
    NewsArticle
  },
  computed: {
    newsList () {
      return contents.sort((news1, news2) => compareDate(news1.date, news2.date))
    }
  }
}
</script>

