<template>
  <div class="course-article--wrap">
    <div class="course-article-title">
      <p class="title section-text-1" v-html="title"
         @click="navigate(recentSemester)"></p>
      <div class="semester--wrap">
        <template v-for="(s, index) in semesterObjectList">
          <p class="semester section-text-4 italic"
             v-html="s.long" :key="s.short"
             @click="navigate(s.short)"></p>
          <p v-if="index < semesterObjectList.length - 1" :key="s.long"
             class="semester-divider section-text-4 italic">/</p>
        </template>
      </div>
    </div>
    <div class="course-article-content">
      <p class="content section-text-2" v-html="content"></p>
    </div>
  </div>
</template>

<script>
import { formSemester } from '@/libs/utils'
import messages from '@/libs/messages'

export default {
  name: 'CourseArticle',
  props: {
    title: {
      type: String,
      default: 'Course Title'
    },
    acronym: {
      type: String,
      default: 'Course Acronym'
    },
    semester: {
      type: Object,
      default () {
        return {}
      }
    },
    content: {
      type: String,
      default: 'Course Content'
    }
  },
  computed: {
    semesterObjectList () {
      let objectList = []
      for (const s in this.semester) {
          objectList.push(formSemester(s))
      }
      return objectList
    },
    recentSemester () {
      return Object.keys(this.semester)[0]
    }
  },
  methods: {
    navigate (semester) {
      if (!this.semester[semester]) {
        alert(messages.closed)
        return
      }

      this.$router.push({
        name: 'Courses.Main',
        params: { course: `${this.acronym}-${semester}` }
      })
    }
  }
}
</script>

