.course-article--wrap {
  float: left;
  width: 100%;
  padding-bottom: 40px;

  .course-article-title {
    float: left;
    width: 100%;
    padding-bottom: 16px;

    .title:hover {
      cursor: pointer;
    }

    .semester--wrap {
      display: inline-block;
      width: 100%;
      float: left;

      .semester {
        float: left;

        &:hover {
          cursor: pointer;
          text-decoration: underline;
        }
      }

      .semester-divider {
        float: left;
        margin: 0 4px;
      }
    }
  }

  .course-article-content {
    float: left;
    width: 100%;

    .content {
      float: left;
      line-height: 24px;
    }
  }
}
</style>
