<template>
  <div class="publication-article--wrap">
    <div class="publication-article-title">
      <p class="title section-text-1" v-html="title"></p>
      <p class="field-and-type section-text-4 italic" v-html="fieldAndType"></p>
      <p class="author section-text-2" v-html="authorAndConference"></p>
      <div v-if="link || file" class="publication-button--wrap">
        <a v-if="link" :href="link" target="_blank">
          <img class="publication-button" src="../../assets/img/icon/link.svg" alt="link">
        </a>
        <a v-if="file" :href="`${filePath}${file}`" target="_self" download>
          <img class="publication-button" src="../../assets/img/icon/download.svg" alt="file">
        </a>
      </div>
    </div>
    <div class="publication-article-content">
      <p class="content section-text-3" v-html="content"></p>
    </div>
  </div>
</template>

<script>
import { PUBLICATIONS_TYPE } from '@/libs/constants'

export default {
  name: 'PublicationArticle',
  props: {
    title: {
      type: String,
      default: 'Publication Title'
    },
    field: {
      type: Array,
      default () {
        return []
      }
    },
    type: {
      type: String,
      default: PUBLICATIONS_TYPE.conferencePaper
    },
    author: {
      type: Array,
      default () {
        return ['Publication Author']
      }
    },
    conference: {
      type: String,
      default: ''
    },
    content: {
      type: String,
      default: 'Publication Content'
    },
    file: {
      type: String,
      default: ''
    },
    link: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      filePath: `${process.env.BASE_URL}contents/publications/file/`
    }
  },
  computed: {
    fieldAndType () {
      return `${this.field.join(', ')} | ${this.type}`
    },
    authorAndConference () {
      let authorString = this.author[0]
      if (this.author.length > 1) {
        for (let i = 1; i < this.author.length; i++) {
          if (i < this.author.length - 1) {
            authorString += `, ${this.author[i]}`
          } else {
            authorString += ` and ${this.author[i]}`
          }
        }
      }

      if (this.conference) {
        return `${authorString} | ${this.conference}`
      }
      return authorString
    }
  }
}
</script>

