import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import People from '../views/People'
import Research from '../views/Research'
import Publications from '../views/Publications'
import Courses from '../views/Courses/index'
import CourseList from '../views/Courses/CourseList'
import CourseDetail from '../views/Courses/CourseDetail/index'
import CourseMain from '../views/Courses/CourseDetail/CourseMain'
import CourseSection from '../views/Courses/CourseDetail/CourseSection'
import News from '../views/News'
import PageNotFound from '../views/PageNotFound'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/people',
    name: 'People',
    component: People
  },
  {
    path: '/research',
    name: 'Research',
    component: Research
  },
  {
    path: '/publications',
    name: 'Publications',
    component: Publications
  },
  {
    path: '/courses',
    component: Courses,
    children: [
      {
        path: '',
        name: 'Courses',
        component: CourseList
      },
      {
        path: ':course',
        component: CourseDetail,
        children: [
          {
            path: '',
            name: 'Courses.Main',
            component: CourseMain
          },
          {
            path: ':section',
            name: 'Courses.Section',
            component: CourseSection
          }
        ]
      }
    ]
  },
  {
    path: '/news',
    name: 'News',
    component: News
  },
  {
    path: '*',
    name: 'PageNotFound',
    component: PageNotFound
  }
]

const scrollBehavior = function (to, from, savedPosition) {
  if (savedPosition) {
    return savedPosition
  } else if (to.hash) {
    return {
      selector: to.hash
    }
  } else {
    return { x: 0, y: 0 }
  }
}

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  scrollBehavior
})

export default router
