import { parse, format, isAfter } from 'date-fns'

const FORMAT_FROM = 'yyyy-MM-dd'
const FORMAT_TO = 'dd MMMM yyyy'

export function convertDate (rawDateStr) {
  return format(parse(rawDateStr, FORMAT_FROM, new Date()), FORMAT_TO)
}

export function compareDate (rawDateStr1, rawDateStr2) {
  return isAfter(parse(rawDateStr1, FORMAT_FROM, new Date()), parse(rawDateStr2, FORMAT_FROM, new Date())) ? -1 : 0
}

export function formSemester (rawSemesterStr) {
  const result = rawSemesterStr.indexOf('s') > -1 ?
    `${rawSemesterStr.slice(0, 4)} Spring` :
    `${rawSemesterStr.slice(0, 4)} Fall`
  return { short: rawSemesterStr, long: result }
}

export function pluralize (str, num) {
  return str + (num > 1 ? 's' : '')
}
