<template>
  <div class="course-detail-contents">
    <h1 class="course-detail-subsection-title" v-html="section"></h1>
    <div v-for="(s, index) in subsections" :key="index" class="course-detail-section">
      <template v-if="s.length === 1">
        <div v-html="s[0]" class="section-content"></div>
      </template>
      <template v-else>
        <h1 class="section-title section-text-1 bold" v-html="s[0]"></h1>
        <div v-html="s[1]" class="section-content"></div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CourseSection',
  data () {
    return {
      acronym: this.$route.params.course.split('-')[0],
      semester: this.$route.params.course.split('-')[1],
      section: this.$route.params.section
    }
  },
  computed: {
    courseDetail () {
      return require(`../../../../public/contents/courses/course-detail/${this.acronym}/text`).default[this.semester]
    },
    subsections () {
      let body = []
      for (let i = 0; i < this.courseDetail.length; i++) {
        if (this.courseDetail[i][0] === this.section) {
          body = this.courseDetail[i][1].body
        }
      }
      return body
    }
  }
}
</script>
