<template>
  <div class="publications">
    <div class="publications-header">
      <div class="publications-image-stat--wrap">
        <img class="publications-image" src="../assets/img/navbar-image/publications.svg" alt="Publications">
        <img class="publications-image-mobile" src="../assets/img/navbar-image/publications-mobile.svg" alt="Publications">
        <div class="divider"></div>
        <div class="publications-stat">
          <p v-for="(value, property) in publicationsStat" :key="property" class="section-text-1 stat">
            {{value}} {{pluralize(property, value)}}
          </p>
        </div>
      </div>
    </div>
    <div class="publications-contents">
      <div v-for="year in years" :key="year" class="publications-contents-section">
        <h1 class="section-heading alone">{{year}}</h1>
        <publication-article v-for="p in contents[year]" :key="p.title"
                             :title="p.title" :field="p.field"
                             :type="p.type" :author="p.author"
                             :conference="p.conference" :content="p.content"
                             :file="p.file" :link="p.link"/>
      </div>
    </div>
  </div>
</template>

<script>
import contents from '../../public/contents/publications/text'
import PublicationArticle from '@/components/publications/PublicationArticle'
import { PUBLICATIONS_TYPE } from '@/libs/constants'
import { pluralize } from '@/libs/utils'

export default {
  name: 'Publications',
  components: {
    PublicationArticle
  },
  data () {
    return {
      contents: contents
    }
  },
  computed: {
    years () {
      return Object.keys(contents).sort((year1, year2) => parseInt(year2, 10) - parseInt(year1, 10))
    },
    publicationsStat () {
      let stat = {}
      for (const publicationType in PUBLICATIONS_TYPE) {
        stat[PUBLICATIONS_TYPE[publicationType]] = 0
      }

      for (let i = 0; i < this.years.length; i++) {
        const publicationsList = this.contents[this.years[i]]
        for (let j = 0; j < publicationsList.length; j++) {
          stat[publicationsList[j].type]++
        }
      }

      return stat
    }
  },
  methods: {
    pluralize: pluralize
  }
}
</script>

