<template>
  <div class="course-detail">
    <div class="course-detail-title">
      <p class="semester section-text-1 italic" v-html="semesterFormat"></p>
      <p v-if="title.en" class="title en section-text-1 bold" v-html="title.en"></p>
      <p v-if="title.ko" class="title ko section-text-1 bold" v-html="title.ko"></p>
    </div>
    <router-view/>
  </div>
</template>

<script>
import courseList from '../../../../public/contents/courses/course-list'
import { formSemester } from '@/libs/utils'
import messages from '@/libs/messages'

export default {
  name: 'CourseDetail',
  data () {
    return {
      acronym: this.$route.params.course.split('-')[0],
      semester: this.$route.params.course.split('-')[1]
    }
  },
  computed: {
    courseDetail () {
      return require(`../../../../public/contents/courses/course-detail/${this.acronym}/text`).default
    },
    title () {
      return this.courseDetail.title
    },
    semesterFormat () {
      return formSemester(this.semester).long
    }
  },
  created () {
    let courses = courseList.graduate
    for (let i = 0; i < courses.length; i++) {
      if (courses[i].acronym === this.acronym && !courses[i].semester[this.semester]) {
        alert(messages.closed)
        this.$router.push({ name: 'Courses' })
        return
      }
    }

    courses = courseList.undergraduate
    for (let i = 0; i < courses.length; i++) {
      if (courses[i].acronym === this.acronym && !courses[i].semester[this.semester]) {
        alert(messages.closed)
        this.$router.push({ name: 'Courses' })
        return
      }
    }
  }
}
</script>

