<template>
  <div class="course-detail-contents">
    <div v-for="(s, index) in sections" :key="index" class="course-detail-section">
      <template v-if="s.length === 1">
        <div v-html="s[0]" class="section-content"></div>
      </template>
      <template v-else-if="isClickable(s[1])">
        <h1 class="section-title clickable section-text-1 bold"
            @click.stop="navigate(s[0])" v-html="s[0]"></h1>
        <div v-html="s[1].content" class="section-content"></div>
      </template>
      <template v-else>
        <h1 class="section-title section-text-1 bold" v-html="s[0]"></h1>
        <div v-html="s[1]" class="section-content"></div>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CourseMain',
  data () {
    return {
      acronym: this.$route.params.course.split('-')[0],
      semester: this.$route.params.course.split('-')[1]
    }
  },
  computed: {
    sections () {
      return require(`../../../../public/contents/courses/course-detail/${this.acronym}/text`).default[this.semester]
    }
  },
  methods: {
    isClickable (s) {
      return typeof s !== 'string'
    },
    navigate (s) {
      this.$router.push({
        name: 'Courses.Section',
        params: { section: s }
      })
    }
  }
}
</script>
